.reviews {
  overflow: hidden;
  &__title {
    @include center-column;
    h2 {
      font-size: 60px;
      font-weight: bold;
      color: $dark-two;
      text-align: center;
      margin-bottom: 25px;
      @include r(767) {
        font-size: 40px; }
      span {
        font-size: 30px;
        display: block;
        @include r(767) {
          font-size: 20px; } } }
    p {
      font-size: 20px;
      font-weight: normal;
      line-height: 1.25;
      text-align: center;
      color: $dark-two;
      max-width: 350px;
      margin-bottom: 70px;
      @include r(767) {
        font-size: 18px; }
      b {
        font-weight: 600;
        span {
          font-weight: 600;
          color: #ff0057; } } } }
  &__content {
    position: relative;
    padding-top: 105px;
    @include r(767) {
      padding-top: 50px; } }
  .swiper-reviews {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    .swiper-slide {
      @include cover; } }
  h3 {
    font-size: 18px;
    font-weight: bold;
    color: #fdfdfd; }
  &__block {
    padding-top: 110px;
    p {
      font-size: 30px;
      font-weight: 600;
      line-height: 1.33;
      color: #fdfdfd;
      margin-bottom: 30px;
      max-width: 370px;
      position: relative;
      @include r(767) {
        font-size: 26px; }
      &:before {
        content: '';
        background-image: url('../img/quote.png');
        width: 126px;
        height: 96px;
        @include cover;
        position: absolute;
        left: -60px;
        top: -45px;
        display: block;
        @include r(1400) {
          left: -10px; } } }
    b {
      font-size: 18px;
      font-weight: normal;
      line-height: 1.39;
      color: #fdfdfd;
      margin-bottom: 20px;
      display: block; }
    &__stars {
      margin-bottom: 100px;
      a {
        margin-right: 5px;
        i {
          color: #fff000;
          font-size: 17px; } } }
    &__stages {
      display: flex;
      justify-content: space-between;
      @include r(767) {
        flex-direction: column; }
      li {
        border: solid 1px rgba($white, 0.4);
        border-bottom: none;
        border-top-right-radius: 10px;
        border-top-left-radius: 10px;
        padding: 20px 35px;
        @include center;
        text-align: center;
        margin: 0 20px;
        font-size: 14px;
        font-weight: bold;
        color: #fdfdfd;
        position: relative;
        @include r(1040) {
          margin: 0 10px;
          padding: 10px 15px; }
        @include r(767) {
          border-radius: 10px;
          margin: 15px 0;
          border: solid 1px rgba($white, 0.4); }
        &:before {
          content: $icon-double-arrows;
          position: absolute;
          right: -28px;
          font-family: "iconfont";
          font-size: 12px;
          color: #ff0057;
          @include r(1040) {
            right: -18px; }
          @include r(767) {
            right: auto;
            left: 50%;
            transform: translate(-50%) rotate(90deg);
            bottom: -22px; } }
        &:last-child {
          &:before {
            display: none; } } } } } }
