.about {
  background: $white;
  padding: 130px 0 50px;
  position: relative;
  &__purchases {
    position: relative;
    width: 100%;
    left: 20px;
    img {
      // position: absolute
      // left: 37%
      // top: 50%
      // margin-left: 40px
      width: 100px; }
    &__block {
      width: 100px;
      height: 100px;
      display: block;
      position: absolute;
      left: 37%;
      top: 50%;
      margin-left: 40px;
      span {
        font-size: 10px;
        font-weight: 900;
        color: $dark-two;
        text-transform: uppercase; } } }
  &__phone {
    width: 515px;
    height: 815px;
    overflow: hidden;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    @include center;
    margin-top: 100px;
    &:before {
      content: '';
      width: 100%;
      background: $white;
      height: 17px;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 19; }
    img.phone {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 20; }
    &-img {
      margin: 0 20px;
      width: calc(100% - 40px);
      left: 0;
      top: 15px;
      position: absolute;
      &:nth-child(2) {
        top: 10px; } }
    .swiper {
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 18;
      background: $white;
      background: url('../img/home/iphone-dc@3x.png');
      background-size: 250px 547px;
      background-repeat: no-repeat;
      background-position: 50% 40%;
      &-container {
        width: calc(100% - 40px);
        padding: 22px 13px 0;
        height: 100%; }
      &-slide {
        @include center;
        opacity: 0.4;
        img {
          width: 100%; }
        &-active {
          opacity: 1; } } } }
  &__text {
    height: 100%;
    @include center-y;
    p {
      font-size: 30px;
      font-weight: normal;
      line-height: 1.67;
      color: #212121;
      text-align: left;
      max-width: 500px;
      span {
        font-weight: bold;
        color: #ff0057; } } } }





